import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-award-employee-preview',
  templateUrl: './award-employee-preview.page.html',
  styleUrls: ['./award-employee-preview.page.scss'],
})
export class AwardEmployeePreviewPage implements OnInit {
  dashData: any;
  templateData: any;
  errorMessage: string;
  priviousSelectedData: any;
  slideOpts = {
    initialSlide: 1,
    speed: 400
  };
  citatonData: any='';
  teamName: string;
  showDiv: boolean=false;
  showImage: any;
  templateID: any;
  slideOpts1 = {
    initialSlide: 0,
    slidesPerView: 2.5,
    speed: 400,
    spaceBetween: 5,
  };
  constructor(private zone: NgZone, private apiservices: ApiservicesService, private stoargeServices: LocalStorageService,
    private sendData: DataTransferService, private router: Router,private navctrl:NavController) { }

  ngOnInit() {
    this.priviousSelectedData=this.sendData.alldata;
    console.log(" this.priviousSelectedData==", this.priviousSelectedData);
    this.awardDashboardFun();
  }

  awardDashboardFun() {
    this.zone.run(async () => {
      this.apiservices.showLoader();
      this.stoargeServices.getStorage().then(storedData => {
        let apiKey = { 
          team_type:this.priviousSelectedData.previous.mainData.team_type
         }
        this.apiservices.apiCallWithLoginToken(URLS.awardTemplateApi, apiKey).subscribe((result) => {
          this.apiservices.hideLoader();
          this.dashData = result;
          if (result.status == 1) {
            this.templateData = result.data;
            this.showImage=this.templateData[0].template_link;
            this.templateID=this.templateData[0].auto_id;
            this.errorMessage = '';
          } else {
            this.errorMessage = result.message;
            // this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
          }

        }, err => {
          this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
          this.apiservices.hideLoader();
        });


      });
    });
  }


  showslidesDiv(){
    this.zone.run(()=>{
      console.log("show filter1==", this.showDiv);
      if (this.showDiv) {
        console.log("show filter2==", this.showDiv);
        this.showDiv = false;
      }else{
        console.log("show filter3==", this.showDiv);
        this.showDiv=true;
      }
    });
  }

  selectedTemp(item){
    this.showDiv = false;
    this.showImage=item.template_link;
    this.templateID=item.auto_id;

  }
  editFun(){
    this.navctrl.pop();
  }

  submitFun(){
    if(this.priviousSelectedData.previous.mainData.team_type==1){
      this.teamName='';
    }
    this.zone.run(async () => {
      this.apiservices.showLoader();
      this.stoargeServices.getStorage().then(storedData => {
        let apiKey = { 
          employee_id: storedData.employeeID,
          citation:this.citatonData,
          award_id: this.priviousSelectedData.previous.mainData.auto_id,
          team_name:this.teamName,
          nominees: this.priviousSelectedData.cur_nominees.join(","),
          template_id:this.templateID
         }
        let copy_apiKey={
          employee_id: storedData.employeeID,
          citation:this.citatonData,
          award_id: this.priviousSelectedData.previous.mainData.auto_id,
          team_name:this.teamName,
          nominees: this.priviousSelectedData.cur_nominees.join(","),
          template_id:this.templateID
        }
        this.apiservices.apiCallWithLoginTokenComment(URLS.awardSubmitApi, apiKey,copy_apiKey).subscribe((result) => {
          this.apiservices.hideLoader();
          if (result.status == 1) {
            this.apiservices.showToastMessage(result.message, 'top', 3000, 'greenBg');
            this.router.navigateByUrl('/award-dashboard');
            // this.navCtrl.popTo(this.navCtrl.getByIndex(this.navCtrl.length()-7));
          } else {
            this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
          }

        }, err => {
          this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
          this.apiservices.hideLoader();
        });


      });
    });

  }

}
